import React, { useEffect, useState } from 'react';
import axios from 'axios';
import { useNavigate } from 'react-router-dom';
import { useAuth } from './AuthContext'; // Asegúrate de que el hook esté bien implementado en el contexto
import './LoginRegister.css';

const LoginRegister = () => {
  const [loginError, setLoginError] = useState('');
  const [registerMessage, setRegisterMessage] = useState('');
  const [isLoginVisible, setIsLoginVisible] = useState(true);
  const navigate = useNavigate();
  const { login } = useAuth(); // Obtiene la función login del contexto

  useEffect(() => {
    const params = new URLSearchParams(window.location.search);
    const messageParam = params.get('message');
    if (messageParam === 'login_error') {
      setLoginError('Correo o contraseña incorrectos.');
    } else if (messageParam === 'register_success') {
      setLoginError('Registro exitoso. Por favor, inicia sesión.');
    }
  }, []);

  const handleLogin = async (e) => {
    e.preventDefault();
    const username = e.target.username.value;
    const password = e.target.password.value;

    try {
      const response = await axios.post(
        'https://api-degusto.onrender.com/api/user/login',
        { userName: username, userPassword: password }
      );

      if (response.data && response.data.id) {
        login(response.data.id); // Guarda el userId en el contexto
        const { person_TypeId, userName, userStatus } = response.data;
        if (userName) {
          if (person_TypeId === 3) {
            navigate('/menuadmin');
          } else if (person_TypeId === 1) {
            if (userStatus === 1) {
              navigate('/menufunctions');
            } else {
              setLoginError('Tu cuenta aún no ha sido aprobada.');
            }
          } else {
            setLoginError('Tipo de usuario no reconocido.');
          }
        }
      } else {
        setLoginError('Usuario o contraseña incorrectos');
      }
    } catch (error) {
      console.error('Error de login:', error);
      setLoginError('Error en el inicio de sesión. Inténtalo de nuevo.');
    }
  };

  const handleRegister = async (e) => {
    e.preventDefault();
    const name = e.target.name.value;
    const email = e.target.email.value;
    const phone = e.target.phone.value;
    const username = e.target.username.value;
    const password = e.target.password.value;

    try {
      await axios.post('https://api-degusto.onrender.com/api/user/register', {
        personTypeId: 1,
        name,
        email,
        phoneNumber: phone,
        userName: username,
        password,
        createDate: new Date().toISOString(),
      });
      setRegisterMessage('Registro exitoso. Espera confirmación.');
      setIsLoginVisible(true);
    } catch (error) {
      console.error('Error en el registro:', error);
      setRegisterMessage('Error en el registro. Revisa los datos.');
    }
  };

  return (
    <main>
      <div className="contenedor__todo">
        <div className="caja__trasera">
          <div className="caja__trasera-login">
            <h3>¿Ya tienes una cuenta?</h3>
            <p>Inicia sesión para entrar en la página</p>
            <button id="btn__iniciar-sesion" onClick={() => setIsLoginVisible(true)}>Iniciar Sesión</button>
          </div>
          <div className="caja__trasera-register">
            <h3>¿Aún no tienes una cuenta?</h3>
            <p>Regístrate para que puedas iniciar sesión</p>
            <button id="btn__registrarse" onClick={() => setIsLoginVisible(false)}>Regístrarse</button>
          </div>
        </div>
        <div className="contenedor__login-register">
          {isLoginVisible ? (
            <form onSubmit={handleLogin} className="formulario__login">
              <h2>Iniciar Sesión</h2>
              <input type="text" name="username" placeholder="Nombre de Usuario" required />
              <input type="password" name="password" placeholder="Contraseña" required />
              <button type="submit">Entrar</button>
              <div id="login-message">{loginError}</div>
            </form>
          ) : (
            <form onSubmit={handleRegister} className="formulario__register">
              <h2>Regístrarse</h2>
              <input type="text" name="name" placeholder="Nombre completo" required />
              <input type="email" name="email" placeholder="Correo Electrónico" required />
              <input type="text" name="phone" placeholder="Número de celular" required />
              <input type="text" name="username" placeholder="Usuario" required />
              <input type="password" name="password" placeholder="Contraseña" required />
              <button type="submit">Regístrarse</button>
              <div id="register-message">{registerMessage}</div>
            </form>
          )}
        </div>
      </div>
    </main>
  );
};

export default LoginRegister;